


*{
box-sizing:  0;

}


body {
    margin:0;
    font-family:Karla, sans-seriff;
}

.nav{
    display:flex;
    justify-content:center;
    align-items: center;
    color:white;
    gap : 10px;
    background-color:#672280;
    padding: 0.75em 0 0.813em 1.25em;
}

.nav--image{
    width: 4.5em;
}

.nav--title{
  
    font-size: 2.125em;
    line-height: 1.5em;
letter-spacing: -0.1em;
}

/* form */

.form{

}

.form--inputs{
    display:flex;
    justify-content:center;
    gap:20px ;
    margin:25px auto 20px auto;
    height:40px;

}

.form--input1{
    width:300px;
    border-radius: 5px;
    border-style:solid;
    border-color: #D5D4D8;

}

.form--input2{
    width:300px;
    border-radius: 5px;
    border-style:solid;
    border-color: #D5D4D8;
   

}

/* parent */
.form--elements{
    margin:auto;
    width:540px;
}

.form--button{
    display:flex;
 
    margin:auto;

    color:white;
    background-color:#672280;
    border:0;
    padding:1.5%;
    width:100%;
    height:50px;
    border-radius: 5px;
 

}

/* code below for button animation */

.form--button{
    transition-duration: .1s;
  }
    
  .form--button:active {
    transform: scale(0.9);

}


/* button animation code ends */



.form--button_text{
    display:flex;
    margin:auto;
    font-size: 20px;
}



.form--image{
    border-radius: 6px;
    margin-top: 30px;
    width:100%;
}



.form--image:hover{
    opacity:0.8;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.text-position{
    position: relative;

}

.bottom {
    position: absolute;
    bottom: 0px;
}

.top {
    position: absolute;

    top: 40px;
}